import React, { createContext, useEffect, useState, useContext } from 'react';

import validate from '../utils/validate';
import { initNewPlayer, initPlayers } from '../games/scorekeeper/PlayerInit';
import golfScoring from '../games/scorekeeper/golfScoring';
import initializeData from '../utils/InitializeData';

export const PlayerContext = createContext();

const PlayerParent = ({
    children,
    targetElementRef
}) => {

    const [players, setPlayerData] = useState([]);
    const [edit, setEdit] = useState(false);

    const setPlayers = (newValue) => {
        setPlayerData(newValue);
    };
    const editPlayer = (index) => {
        const newPlayer = prompt('Enter new name', players[index].name);
        if (newPlayer !== null) {
            let newPlayers = [...players];
            newPlayers[index].name = newPlayer;
            localStorage.setItem('players', JSON.stringify(newPlayers));
            setPlayers(initializeData('players', initPlayers));
        }
    }
    useEffect(() => {
        const localPlayers = initializeData('players', initPlayers);
        const newPlayers = [...localPlayers];
        if (!newPlayers[0].cricketScores || !newPlayers[1].cricketScores) {
            if (!newPlayers[0].cricketScores) {
                newPlayers[0].cricketScores = [0, 0, 0, 0, 0, 0, 0];
                newPlayers[0].dartsScore = 0;
            }
            if (!newPlayers[1].cricketScores) {
                newPlayers[1].cricketScores = [0, 0, 0, 0, 0, 0, 0];
                newPlayers[1].dartsScore = 0;
            }
        }
        const initGolfStats = [false, false, false, false, false, false, false, false, false, false, false, false, false, false, false, false, false, false]
        if (!newPlayers[0].golfPutts || !newPlayers[0].golfFW || !newPlayers[0].golfGIR) {
            newPlayers[0].golfPutts = golfScoring;
            newPlayers[0].golfFW = initGolfStats;
            newPlayers[0].golfGIR = initGolfStats;
        }
        if (!newPlayers[1].golfPutts || !newPlayers[1].golfFW || !newPlayers[1].golfGIR) {
            newPlayers[1].golfPutts = golfScoring;
            newPlayers[1].golfFW = initGolfStats;
            newPlayers[1].golfGIR = initGolfStats;
        }
        setPlayers(newPlayers);
    }, []);

    useEffect(() => {
        if (JSON.stringify(players) !== '[]' && validate(players) !== null) {
            const surfersScores = [];
            localStorage.setItem('players', JSON.stringify(players));
            localStorage.setItem('surfersScores', JSON.stringify(surfersScores));
            //setShowButtons(false);
            setEdit(false);
        }
    }, [players]);
    useEffect(() => {
        console.log(`edit: ${edit}`);
    }, [edit]);
    
    const deletePlayer = (index) => {
        const newPlayers = [...players].slice();
        newPlayers.splice(index, 1);
        localStorage.setItem('players', JSON.stringify(newPlayers));
        setPlayers(initializeData('players', initPlayers));
    };

    return (
        
        <PlayerContext.Provider value={{
            players,
            setPlayers,
            editPlayer,
            edit,
            setEdit,
            deletePlayer,
            targetElementRef
        }}>
            {
                (validate(players) !== null)
                    ? children
                    : <div>WHOOOPSIE!</div>
            }
        </PlayerContext.Provider>
    );
};
export const usePlayers = () => useContext(PlayerContext);

export default PlayerParent;